/* eslint-disable */
import React from "react";
import { IoShareSocialSharp, IoStar } from "react-icons/io5";

import { Song } from "../../types/song";
import { GuessType } from "../../types/guess";
import { scoreToEmoji } from "../../helpers";

import { Button } from "../Button";
import { YouTube } from "../YouTube";

import * as Styled from "./index.styled";

interface Props {
  didGuess: boolean;
  currentTry: number;
  todaysSolution: Song;
  guesses: GuessType[];
}

export function Result({
  didGuess,
  todaysSolution,
  guesses,
  currentTry,
}: Props) {
  const hoursToNextDay = Math.floor(
    (new Date(new Date().setHours(24, 0, 0, 0)).getTime() -
      new Date().getTime()) /
      1000 /
      60 /
      60
  );

  if (didGuess) {
    const result = [];
    for (var i = 0; i < currentTry; i++){
      if (i == currentTry-1){
        result.push(<IoStar color="4DBB60"/>);
        break;
      }
      result.push(<IoStar color="BB4D4D"/>)
    }
    for (var i = currentTry; i < 5; i++){
      result.push(<IoStar color="E6E6E6"/>)
    }

    const copyResult = React.useCallback(() => {
      navigator.clipboard.writeText(scoreToEmoji(guesses));
    }, [guesses]);

    const triesConjugation = currentTry === 1 ? "second" : "seconds";
    const totalSeconds = ['1', '2', '4', '7', '11'];

    return (
      <>
        <Styled.ResultTitle>Congratulations!</Styled.ResultTitle>
        <YouTube id={todaysSolution.youtubeId} />
        The song today was <br /> 
        <Styled.SongTitle>
          {todaysSolution.name} -{" "}
          {todaysSolution.artist}
        </Styled.SongTitle>
        <Styled.Stars>{result}</Styled.Stars>
        <Styled.Tries>
          You got today's Ainana Heardle in {totalSeconds[currentTry-1]} {triesConjugation}.
        </Styled.Tries>
        <Button onClick={copyResult} variant="green">
          Share <IoShareSocialSharp size="20"/>
        </Button>
        <Styled.TimeToNext>
          The next song refresh is in {hoursToNextDay}{" "}
          hour(s)!
        </Styled.TimeToNext>
      </>
    );
  } else {
    const result = [];
    for (var i = 0; i < 5; i++){
      result.push(<IoStar color="BB4D4D"/>)
    }
    return (
      <>
        <Styled.ResultTitle>Unfortunately...</Styled.ResultTitle>
        <YouTube id={todaysSolution.youtubeId} />
        The song today was <br /> 
        <Styled.SongTitle>
          {todaysSolution.name} -{" "}
          {todaysSolution.artist}
        </Styled.SongTitle>
        <Styled.Stars>{result}</Styled.Stars>
        <Styled.TimeToNext>
          The next song refresh is in {hoursToNextDay}{" "}
          hour(s)!
        </Styled.TimeToNext>
      </>
    );
  }
}